<template>
  <div>
    <el-form
      class="detail-form-content"
      ref="ruleForm"
      :model="ruleForm"
      label-width="80px"
    >  
     <el-row>
                    <el-col :span="12">
           <el-form-item v-if="flag=='zuke'"  label='' prop="zukeName">
               <el-input v-model="ruleForm.zukeName"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='zuke'"  label='' prop="zukePhone">
               <el-input v-model="ruleForm.zukePhone"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='zuke'"  label='' prop="zukeIdNumber">
               <el-input v-model="ruleForm.zukeIdNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
             <el-form-item v-if="flag=='zuke'" label='' prop="zukePhoto">
                 <file-upload
                         tip=""
                         action="file/upload"
                         :limit="3"
                         :multiple="true"
                         :fileUrls="ruleForm.zukePhoto?ruleForm.zukePhoto:''"
                         @change="zukePhotoUploadChange"
                 ></file-upload>
             </el-form-item>
         </el-col>
         <el-col :span="12">
           <el-form-item v-if="flag=='zuke'"  label='' prop="zukeEmail">
               <el-input v-model="ruleForm.zukeEmail"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='fangzhu'"  label='' prop="fangzhuName">
               <el-input v-model="ruleForm.fangzhuName"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='fangzhu'"  label='' prop="fangzhuPhone">
               <el-input v-model="ruleForm.fangzhuPhone"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='fangzhu'"  label='' prop="fangzhuIdNumber">
               <el-input v-model="ruleForm.fangzhuIdNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
             <el-form-item v-if="flag=='fangzhu'" label='' prop="fangzhuPhoto">
                 <file-upload
                         tip=""
                         action="file/upload"
                         :limit="3"
                         :multiple="true"
                         :fileUrls="ruleForm.fangzhuPhoto?ruleForm.fangzhuPhoto:''"
                         @change="fangzhuPhotoUploadChange"
                 ></file-upload>
             </el-form-item>
         </el-col>
         <el-col :span="12">
           <el-form-item v-if="flag=='fangzhu'"  label='' prop="fangzhuEmail">
               <el-input v-model="ruleForm.fangzhuEmail"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='kefu'"  label='' prop="kefuName">
               <el-input v-model="ruleForm.kefuName"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='kefu'"  label='' prop="kefuPhone">
               <el-input v-model="ruleForm.kefuPhone"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='kefu'"  label='' prop="kefuIdNumber">
               <el-input v-model="ruleForm.kefuIdNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
             <el-form-item v-if="flag=='kefu'" label='' prop="kefuPhoto">
                 <file-upload
                         tip=""
                         action="file/upload"
                         :limit="3"
                         :multiple="true"
                         :fileUrls="ruleForm.kefuPhoto?ruleForm.kefuPhoto:''"
                         @change="kefuPhotoUploadChange"
                 ></file-upload>
             </el-form-item>
         </el-col>
         <el-col :span="12">
           <el-form-item v-if="flag=='kefu'"  label='' prop="kefuEmail">
               <el-input v-model="ruleForm.kefuEmail"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='weixiurenyuan'"  label='' prop="weixiurenyuanName">
               <el-input v-model="ruleForm.weixiurenyuanName"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='weixiurenyuan'"  label='' prop="weixiurenyuanPhone">
               <el-input v-model="ruleForm.weixiurenyuanPhone"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='weixiurenyuan'"  label='' prop="weixiurenyuanIdNumber">
               <el-input v-model="ruleForm.weixiurenyuanIdNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
             <el-form-item v-if="flag=='weixiurenyuan'" label='' prop="weixiurenyuanPhoto">
                 <file-upload
                         tip=""
                         action="file/upload"
                         :limit="3"
                         :multiple="true"
                         :fileUrls="ruleForm.weixiurenyuanPhoto?ruleForm.weixiurenyuanPhoto:''"
                         @change="weixiurenyuanPhotoUploadChange"
                 ></file-upload>
             </el-form-item>
         </el-col>
         <el-col :span="12">
           <el-form-item v-if="flag=='weixiurenyuan'"  label='' prop="weixiurenyuanEmail">
               <el-input v-model="ruleForm.weixiurenyuanEmail"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-form-item v-if="flag=='users'" label="" prop="username">
             <el-input v-model="ruleForm.username"
                       placeholder=""></el-input>
         </el-form-item>
         <el-col :span="12">
             <el-form-item v-if="flag!='users'"  label="" prop="sexTypes">
                 <el-select v-model="ruleForm.sexTypes" placeholder="">
                     <el-option
                             v-for="(item,index) in sexTypesOptions"
                             v-bind:key="item.codeIndex"
                             :label="item.indexName"
                             :value="item.codeIndex">
                     </el-option>
                 </el-select>
             </el-form-item>
         </el-col>
         <el-col :span="24">
             <el-form-item>
                 <el-button type="primary" @click="onUpdateHandler"> </el-button>
             </el-form-item>
         </el-col>
     </el-row>
    </el-form>
  </div>
</template>
<script>
// url
import { isNumber,isIntNumer,isEmail,isMobile,isPhone,isURL,checkIdCard } from "@/utils/validate";

export default {
  data() {
    return {
        ruleForm: {},
        flag: '',
        usersFlag: false,
        // sexTypesOptions : [],
//  
    //  
        // 
        sexTypesOptions : [],
//  
    //  
        // 
        sexTypesOptions : [],
//  
    //  
        // 
        sexTypesOptions : [],
//  
    //  
        // 
        sexTypesOptions : [],
    };
  },
  mounted() {
    //
    var table = this.$storage.get("sessionTable");
    this.sessionTable = this.$storage.get("sessionTable");
    this.role = this.$storage.get("role");
    if (this.role != ""){
    }

    this.flag = table;
    this.$http({
      url: `${this.$storage.get("sessionTable")}/session`,
      method: "get"
    }).then(({ data }) => {
      if (data && data.code === 0) {
        this.ruleForm = data.data;
//  
//  
//  
//  
      } else {
        this.$message.error(data.msg);
      }
    });

//   
//   
//   
//   

      this.$http({
          url: `dictionary/page?page=1&limit=100&sort=&order=&dicCode=sex_types`,
          method: "get"
      }).then(({ data }) => {
          if (data && data.code === 0) {
              this.sexTypesOptions = data.data.list;
          } else {
              this.$message.error(data.msg);
          }
      });
  },
  methods: {
    chongzhi() {
      this.$router.replace({ path: "/pay" });
    },
    zukePhotoUploadChange(fileUrls) {
        this.ruleForm.zukePhoto = fileUrls;
    },
    fangzhuPhotoUploadChange(fileUrls) {
        this.ruleForm.fangzhuPhoto = fileUrls;
    },
    kefuPhotoUploadChange(fileUrls) {
        this.ruleForm.kefuPhoto = fileUrls;
    },
    weixiurenyuanPhotoUploadChange(fileUrls) {
        this.ruleForm.weixiurenyuanPhoto = fileUrls;
    },


    onUpdateHandler() {
                         if((!this.ruleForm.zukeName)&& 'zuke'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'zuke' ==this.flag && this.ruleForm.zukePhone&&(!isMobile(this.ruleForm.zukePhone))){
                                 this.$message.error(``);
                                 return
                             }
                         if((!this.ruleForm.zukeIdNumber)&& 'zuke'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.zukePhoto)&& 'zuke'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'zuke' ==this.flag && this.ruleForm.zukeEmail&&(!isEmail(this.ruleForm.zukeEmail))){
                                 this.$message.error(``);
                                 return
                             }
                         if((!this.ruleForm.fangzhuName)&& 'fangzhu'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'fangzhu' ==this.flag && this.ruleForm.fangzhuPhone&&(!isMobile(this.ruleForm.fangzhuPhone))){
                                 this.$message.error(``);
                                 return
                             }
                         if((!this.ruleForm.fangzhuIdNumber)&& 'fangzhu'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.fangzhuPhoto)&& 'fangzhu'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'fangzhu' ==this.flag && this.ruleForm.fangzhuEmail&&(!isEmail(this.ruleForm.fangzhuEmail))){
                                 this.$message.error(``);
                                 return
                             }
                         if((!this.ruleForm.kefuName)&& 'kefu'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'kefu' ==this.flag && this.ruleForm.kefuPhone&&(!isMobile(this.ruleForm.kefuPhone))){
                                 this.$message.error(``);
                                 return
                             }
                         if((!this.ruleForm.kefuIdNumber)&& 'kefu'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.kefuPhoto)&& 'kefu'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'kefu' ==this.flag && this.ruleForm.kefuEmail&&(!isEmail(this.ruleForm.kefuEmail))){
                                 this.$message.error(``);
                                 return
                             }
                         if((!this.ruleForm.weixiurenyuanName)&& 'weixiurenyuan'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'weixiurenyuan' ==this.flag && this.ruleForm.weixiurenyuanPhone&&(!isMobile(this.ruleForm.weixiurenyuanPhone))){
                                 this.$message.error(``);
                                 return
                             }
                         if((!this.ruleForm.weixiurenyuanIdNumber)&& 'weixiurenyuan'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.weixiurenyuanPhoto)&& 'weixiurenyuan'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'weixiurenyuan' ==this.flag && this.ruleForm.weixiurenyuanEmail&&(!isEmail(this.ruleForm.weixiurenyuanEmail))){
                                 this.$message.error(``);
                                 return
                             }
        if((!this.ruleForm.sexTypes) && this.flag!='users'){
            this.$message.error('');
            return
        }
      if('users'==this.flag && this.ruleForm.username.trim().length<1) {
        this.$message.error(``);
        return	
      }
      this.$http({
        url: `${this.$storage.get("sessionTable")}/update`,
        method: "post",
        data: this.ruleForm
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.$message({
            message: "",
            type: "success",
            duration: 1500,
            onClose: () => {
            }
          });
        } else {
          this.$message.error(data.msg);
        }
      });
    }
  }
};
</script>
