<template>
    <div class="addEdit-block">
        <el-form
                class="detail-form-content"
                ref="ruleForm"
                :model="ruleForm"
                :rules="rules"
                label-width="auto"
                :style="{backgroundColor:addEditForm.addEditBoxColor}">
            <el-row>
                <el-col :span="12"  v-if="sessionTable !='fangzhu'">
                    <el-form-item class="select" v-if="type!='info'"  label="" prop="fangzhuId">
                        <el-select v-model="ruleForm.fangzhuId" :disabled="ro.fangzhuId" filterable placeholder="" @change="fangzhuChange">
                            <el-option
                                    v-for="(item,index) in fangzhuOptions"
                                    v-bind:key="item.id"
                                    :label="item.fangzhuName"
                                    :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>

                <el-col :span="12"  v-if="sessionTable !='fangzhu' ">
                    <el-form-item class="input" v-if="type!='info'"  label="" prop="fangzhuName">
                        <el-input v-model="fangzhuForm.fangzhuName"
                                  placeholder="" clearable readonly></el-input>
                    </el-form-item>
                    <div v-else-if="type=='info'">
                        <el-form-item class="input" label="" prop="fangzhuName">
                            <el-input v-model="ruleForm.fangzhuName"
                                      placeholder="" readonly></el-input>
                        </el-form-item>
                    </div>
                </el-col>
                <el-col :span="12"  v-if="sessionTable !='fangzhu' ">
                    <el-form-item class="input" v-if="type!='info'"  label="" prop="fangzhuPhone">
                        <el-input v-model="fangzhuForm.fangzhuPhone"
                                  placeholder="" clearable readonly></el-input>
                    </el-form-item>
                    <div v-else-if="type=='info'">
                        <el-form-item class="input" label="" prop="fangzhuPhone">
                            <el-input v-model="ruleForm.fangzhuPhone"
                                      placeholder="" readonly></el-input>
                        </el-form-item>
                    </div>
                </el-col>
                <el-col :span="12"  v-if="sessionTable !='fangzhu' ">
                    <el-form-item class="input" v-if="type!='info'"  label="" prop="fangzhuIdNumber">
                        <el-input v-model="fangzhuForm.fangzhuIdNumber"
                                  placeholder="" clearable readonly></el-input>
                    </el-form-item>
                    <div v-else-if="type=='info'">
                        <el-form-item class="input" label="" prop="fangzhuIdNumber">
                            <el-input v-model="ruleForm.fangzhuIdNumber"
                                      placeholder="" readonly></el-input>
                        </el-form-item>
                    </div>
                </el-col>
                <el-col :span="12" v-if="sessionTable !='fangzhu' ">
                    <el-form-item class="upload" v-if="type!='info' && !ro.fangzhuPhoto" label="" prop="fangzhuPhoto">
                        <img style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in (fangzhuForm.fangzhuPhoto || '').split(',')" :src="item" width="100" height="100">
                    </el-form-item>
                    <div v-else-if="type=='info'">
                        <el-form-item v-if="ruleForm.fangzhuPhoto" label="" prop="fangzhuPhoto">
                            <img style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in (ruleForm.fangzhuPhoto || '').split(',')" :src="item" width="100" height="100">
                        </el-form-item>
                    </div>
                </el-col>
                <el-col :span="12"  v-if="sessionTable !='fangzhu' ">
                    <el-form-item class="input" v-if="type!='info'"  label="" prop="fangzhuEmail">
                        <el-input v-model="fangzhuForm.fangzhuEmail"
                                  placeholder="" clearable readonly></el-input>
                    </el-form-item>
                    <div v-else-if="type=='info'">
                        <el-form-item class="input" label="" prop="fangzhuEmail">
                            <el-input v-model="ruleForm.fangzhuEmail"
                                      placeholder="" readonly></el-input>
                        </el-form-item>
                    </div>
                </el-col>
                <input id="updateId" name="id" type="hidden">
            <input id="fangzhuId" name="fangzhuId" type="hidden">
               <el-col :span="12">
                   <el-form-item class="input" v-if="type!='info'"  label="" prop="fangwuName">
                       <el-input v-model="ruleForm.fangwuName"
                                 placeholder="" clearable  :readonly="ro.fangwuName"></el-input>
                   </el-form-item>
                   <div v-else-if="type=='info'">
                       <el-form-item class="input" label="" prop="fangwuName">
                           <el-input v-model="ruleForm.fangwuName"
                                     placeholder="" readonly></el-input>
                       </el-form-item>
                   </div>
               </el-col>
               <el-col :span="12">
                   <el-form-item class="input" v-if="type!='info'"  label="" prop="fangwuUuidNumber">
                       <el-input v-model="ruleForm.fangwuUuidNumber"
                                 placeholder="" clearable  :readonly="ro.fangwuUuidNumber"></el-input>
                   </el-form-item>
                   <div v-else-if="type=='info'">
                       <el-form-item class="input" label="" prop="fangwuUuidNumber">
                           <el-input v-model="ruleForm.fangwuUuidNumber"
                                     placeholder="" readonly></el-input>
                       </el-form-item>
                   </div>
               </el-col>
                <el-col :span="12">
                    <el-form-item class="upload" v-if="type!='info' && !ro.fangwuPhoto" label="" prop="fangwuPhoto">
                        <file-upload
                            tip=""
                            action="file/upload"
                            :limit="3"
                            :multiple="true"
                            :fileUrls="ruleForm.fangwuPhoto?ruleForm.fangwuPhoto:''"
                            @change="fangwuPhotoUploadChange"
                        ></file-upload>
                    </el-form-item>
                    <div v-else>
                        <el-form-item v-if="ruleForm.fangwuPhoto" label="" prop="fangwuPhoto">
                            <img style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in (ruleForm.fangwuPhoto || '').split(',')" :src="item" width="100" height="100">
                        </el-form-item>
                    </div>
                </el-col>
                <el-col :span="12">
                    <el-form-item class="select" v-if="type!='info'"  label="" prop="fangwuTypes">
                        <el-select v-model="ruleForm.fangwuTypes" :disabled="ro.fangwuTypes" placeholder="">
                            <el-option
                                v-for="(item,index) in fangwuTypesOptions"
                                v-bind:key="item.codeIndex"
                                :label="item.indexName"
                                :value="item.codeIndex">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <div v-else-if="type=='info'">
                        <el-form-item class="input" label="" prop="fangwuValue">
                        <el-input v-model="ruleForm.fangwuValue"
                            placeholder="" readonly></el-input>
                        </el-form-item>
                    </div>
                </el-col>
               <el-col :span="12">
                   <el-form-item class="input" v-if="type!='info'"  label="" prop="fangwuAddress">
                       <el-input v-model="ruleForm.fangwuAddress"
                                 placeholder="" clearable  :readonly="ro.fangwuAddress"></el-input>
                   </el-form-item>
                   <div v-else-if="type=='info'">
                       <el-form-item class="input" label="" prop="fangwuAddress">
                           <el-input v-model="ruleForm.fangwuAddress"
                                     placeholder="" readonly></el-input>
                       </el-form-item>
                   </div>
               </el-col>
               <el-col :span="12">
                   <el-form-item class="input" v-if="type!='info'"  label="" prop="fangwuMianji">
                       <el-input v-model="ruleForm.fangwuMianji"
                                 placeholder="" clearable  :readonly="ro.fangwuMianji"></el-input>
                   </el-form-item>
                   <div v-else-if="type=='info'">
                       <el-form-item class="input" label="" prop="fangwuMianji">
                           <el-input v-model="ruleForm.fangwuMianji"
                                     placeholder="" readonly></el-input>
                       </el-form-item>
                   </div>
               </el-col>
               <el-col :span="12">
                   <el-form-item class="input" v-if="type!='info'"  label="/" prop="fangwuPingmiJine">
                       <el-input v-model="ruleForm.fangwuPingmiJine"
                                 placeholder="/" clearable  :readonly="ro.fangwuPingmiJine"></el-input>
                   </el-form-item>
                   <div v-else-if="type=='info'">
                       <el-form-item class="input" label="/" prop="fangwuPingmiJine">
                           <el-input v-model="ruleForm.fangwuPingmiJine"
                                     placeholder="/" readonly></el-input>
                       </el-form-item>
                   </div>
               </el-col>
               <el-col :span="12">
                   <el-form-item class="input" v-if="type!='info'"  label="" prop="fangwuOldMoney">
                       <el-input v-model="ruleForm.fangwuOldMoney"
                                 placeholder="" clearable  :readonly="ro.fangwuOldMoney"></el-input>
                   </el-form-item>
                   <div v-else-if="type=='info'">
                       <el-form-item class="input" label="" prop="fangwuOldMoney">
                           <el-input v-model="ruleForm.fangwuOldMoney"
                                     placeholder="" readonly></el-input>
                       </el-form-item>
                   </div>
               </el-col>
               <el-col :span="12">
                   <el-form-item class="input" v-if="type!='info'"  label="" prop="fangwuNewMoney">
                       <el-input v-model="ruleForm.fangwuNewMoney"
                                 placeholder="" clearable  :readonly="ro.fangwuNewMoney"></el-input>
                   </el-form-item>
                   <div v-else-if="type=='info'">
                       <el-form-item class="input" label="" prop="fangwuNewMoney">
                           <el-input v-model="ruleForm.fangwuNewMoney"
                                     placeholder="" readonly></el-input>
                       </el-form-item>
                   </div>
               </el-col>
                <el-col :span="24">
                    <el-form-item v-if="type!='info'"  label="" prop="fangwuContent">
                        <editor style="min-width: 200px; max-width: 600px;"
                                v-model="ruleForm.fangwuContent"
                                class="editor"
                                action="file/upload">
                        </editor>
                    </el-form-item>
                    <div v-else-if="type=='info'">
                        <el-form-item v-if="ruleForm.fangwuContent" label="" prop="fangwuContent">
                            <span v-html="ruleForm.fangwuContent"></span>
                        </el-form-item>
                    </div>
                </el-col>
            </el-row>
            <el-form-item class="btn">
                <el-button v-if="type!='info'" type="primary" class="btn-success" @click="onSubmit"></el-button>
                <el-button v-if="type!='info'" class="btn-close" @click="back()"></el-button>
                <el-button v-if="type=='info'" class="btn-close" @click="back()"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
    import styleJs from "../../../utils/style.js";
    // url
    import { isNumber,isIntNumer,isEmail,isPhone, isMobile,isURL,checkIdCard } from "@/utils/validate";
    export default {
        data() {
            return {
                addEditForm:null,
                id: '',
                type: '',
                sessionTable : "",//
                role : "",//
                userId:"",//id
                fangzhuForm: {},
                ro:{
                    fangzhuId: false,
                    fangwuName: false,
                    fangwuUuidNumber: false,
                    fangwuPhoto: false,
                    fangwuTypes: false,
                    fangwuAddress: false,
                    fangwuMianji: false,
                    fangwuPingmiJine: false,
                    fangwuOldMoney: false,
                    fangwuNewMoney: false,
                    fangwuClicknum: false,
                    fangwuContent: false,
                    shangxiaTypes: false,
                    fangwuDelete: false,
                },
                ruleForm: {
                    fangzhuId: '',
                    fangwuName: '',
                    fangwuUuidNumber: new Date().getTime(),
                    fangwuPhoto: '',
                    fangwuTypes: '',
                    fangwuAddress: '',
                    fangwuMianji: '',
                    fangwuPingmiJine: '',
                    fangwuOldMoney: '',
                    fangwuNewMoney: '',
                    fangwuClicknum: '',
                    fangwuContent: '',
                    shangxiaTypes: '',
                    fangwuDelete: '',
                },
                fangwuTypesOptions : [],
                shangxiaTypesOptions : [],
                fangzhuOptions : [],
                rules: {
                   fangzhuId: [
                              { required: true, message: '', trigger: 'blur' },
                              {  pattern: /^[0-9]*$/,
                                  message: '',
                                  trigger: 'blur'
                              }
                          ],
                   fangwuName: [
                              { required: true, message: '', trigger: 'blur' },
                          ],
                   fangwuUuidNumber: [
                              { required: true, message: '', trigger: 'blur' },
                          ],
                   fangwuPhoto: [
                              { required: true, message: '', trigger: 'blur' },
                          ],
                   fangwuTypes: [
                              { required: true, message: '', trigger: 'blur' },
                              {  pattern: /^[0-9]*$/,
                                  message: '',
                                  trigger: 'blur'
                              }
                          ],
                   fangwuAddress: [
                              { required: true, message: '', trigger: 'blur' },
                          ],
                   fangwuMianji: [
                              { required: true, message: '', trigger: 'blur' },
                              {  pattern: /^[0-9]{0,6}(\.[0-9]{1,2})?$/,
                                  message: '6,2',
                                  trigger: 'blur'
                              }
                          ],
                   fangwuPingmiJine: [
                              { required: true, message: '/', trigger: 'blur' },
                              {  pattern: /^[0-9]{0,6}(\.[0-9]{1,2})?$/,
                                  message: '6,2',
                                  trigger: 'blur'
                              }
                          ],
                   fangwuOldMoney: [
                              { required: true, message: '', trigger: 'blur' },
                              {  pattern: /^[0-9]{0,6}(\.[0-9]{1,2})?$/,
                                  message: '6,2',
                                  trigger: 'blur'
                              }
                          ],
                   fangwuNewMoney: [
                              { required: true, message: '', trigger: 'blur' },
                              {  pattern: /^[0-9]{0,6}(\.[0-9]{1,2})?$/,
                                  message: '6,2',
                                  trigger: 'blur'
                              }
                          ],
                   fangwuClicknum: [
                              { required: true, message: '', trigger: 'blur' },
                              {  pattern: /^[0-9]*$/,
                                  message: '',
                                  trigger: 'blur'
                              }
                          ],
                   fangwuContent: [
                              { required: true, message: '', trigger: 'blur' },
                          ],
                   shangxiaTypes: [
                              { required: true, message: '', trigger: 'blur' },
                              {  pattern: /^[0-9]*$/,
                                  message: '',
                                  trigger: 'blur'
                              }
                          ],
                   fangwuDelete: [
                              { required: true, message: '', trigger: 'blur' },
                              {  pattern: /^[0-9]*$/,
                                  message: '',
                                  trigger: 'blur'
                              }
                          ],
                }
            };
        },
        props: ["parent"],
        computed: {
        },
        created() {
            //
            this.sessionTable = this.$storage.get("sessionTable");
            this.role = this.$storage.get("role");
            this.userId = this.$storage.get("userId");


            if (this.role != ""){
            }
            this.addEditForm = styleJs.addStyle();
            this.addEditStyleChange()
            this.addEditUploadStyleChange()
            //
                this.$http({
                    url:`dictionary/page?page=1&limit=100&sort=&order=&dicCode=fangwu_types`,
                    method: "get"
                }).then(({ data }) => {
                    if (data && data.code === 0) {
                        this.fangwuTypesOptions = data.data.list;
                    }
                });
                this.$http({
                    url:`dictionary/page?page=1&limit=100&sort=&order=&dicCode=shangxia_types`,
                    method: "get"
                }).then(({ data }) => {
                    if (data && data.code === 0) {
                        this.shangxiaTypesOptions = data.data.list;
                    }
                });

         this.$http({
             url: `fangzhu/page?page=1&limit=100`,
             method: "get"
         }).then(({ data }) => {
             if (data && data.code === 0) {
                this.fangzhuOptions = data.data.list;
            }
         });

        },
        mounted() {
        },
        methods: {
            // 
            download(file){
                window.open(`${file}`)
            },
            // 
            init(id,type) {
                if (id) {
                    this.id = id;
                    this.type = type;
                }
                if(this.type=='info'||this.type=='else'){
                    this.info(id);
                }
                // 
                this.$http({
                    url:`${this.$storage.get("sessionTable")}/session`,
                    method: "get"
                }).then(({ data }) => {
                    if (data && data.code === 0) {
                        var json = data.data;
                    } else {
                        this.$message.error(data.msg);
                    }
                });
            },
            fangzhuChange(id){
                this.$http({
                    url: `fangzhu/info/`+id,
                    method: "get"
                }).then(({ data }) => {
                    if (data && data.code === 0) {
                        this.fangzhuForm = data.data;
                    }
                });
            },
            // 
            info(id) {
                let _this =this;
                _this.$http({
                    url: `fangwu/info/${id}`,
                    method: 'get'
                }).then(({ data }) => {
                    if (data && data.code === 0) {
                        _this.ruleForm = data.data;
                        _this.fangzhuChange(data.data.fangzhuId)
                    } else {
                        _this.$message.error(data.msg);
                    }
                });
            },
            // 
            onSubmit() {
                this.$refs["ruleForm"].validate(valid => {
                    if (valid) {
                        this.$http({
                            url:`fangwu/${!this.ruleForm.id ? "save" : "update"}`,
                            method: "post",
                            data: this.ruleForm
                        }).then(({ data }) => {
                            if (data && data.code === 0) {
                                this.$message({
                                    message: "",
                                    type: "success",
                                    duration: 1500,
                                    onClose: () => {
                                        this.parent.showFlag = true;
                                        this.parent.addOrUpdateFlag = false;
                                        this.parent.fangwuCrossAddOrUpdateFlag = false;
                                        this.parent.search();
                                        this.parent.contentStyleChange();
                                    }
                                });
                            } else {
                                this.$message.error(data.msg);
                            }
                        });
                    }
                });
            },
            // uuid
            getUUID () {
                return new Date().getTime();
            },
            // 
            back() {
                this.parent.showFlag = true;
                this.parent.addOrUpdateFlag = false;
                this.parent.fangwuCrossAddOrUpdateFlag = false;
                this.parent.contentStyleChange();
            },
            //
            fangwuPhotoUploadChange(fileUrls){
                this.ruleForm.fangwuPhoto = fileUrls;
                this.addEditUploadStyleChange()
            },

            addEditStyleChange() {
                this.$nextTick(()=>{
                    // input
                    document.querySelectorAll('.addEdit-block .input .el-input__inner').forEach(el=>{
                        el.style.height = this.addEditForm.inputHeight
                        el.style.color = this.addEditForm.inputFontColor
                        el.style.fontSize = this.addEditForm.inputFontSize
                        el.style.borderWidth = this.addEditForm.inputBorderWidth
                        el.style.borderStyle = this.addEditForm.inputBorderStyle
                        el.style.borderColor = this.addEditForm.inputBorderColor
                        el.style.borderRadius = this.addEditForm.inputBorderRadius
                        el.style.backgroundColor = this.addEditForm.inputBgColor
                    })
                    document.querySelectorAll('.addEdit-block .input .el-form-item__label').forEach(el=>{
                        el.style.lineHeight = this.addEditForm.inputHeight
                        el.style.color = this.addEditForm.inputLableColor
                        el.style.fontSize = this.addEditForm.inputLableFontSize
                    })
                    // select
                    document.querySelectorAll('.addEdit-block .select .el-input__inner').forEach(el=>{
                        el.style.height = this.addEditForm.selectHeight
                        el.style.color = this.addEditForm.selectFontColor
                        el.style.fontSize = this.addEditForm.selectFontSize
                        el.style.borderWidth = this.addEditForm.selectBorderWidth
                        el.style.borderStyle = this.addEditForm.selectBorderStyle
                        el.style.borderColor = this.addEditForm.selectBorderColor
                        el.style.borderRadius = this.addEditForm.selectBorderRadius
                        el.style.backgroundColor = this.addEditForm.selectBgColor
                    })
                    document.querySelectorAll('.addEdit-block .select .el-form-item__label').forEach(el=>{
                        el.style.lineHeight = this.addEditForm.selectHeight
                        el.style.color = this.addEditForm.selectLableColor
                        el.style.fontSize = this.addEditForm.selectLableFontSize
                    })
                    document.querySelectorAll('.addEdit-block .select .el-select__caret').forEach(el=>{
                        el.style.color = this.addEditForm.selectIconFontColor
                        el.style.fontSize = this.addEditForm.selectIconFontSize
                    })
                    // date
                    document.querySelectorAll('.addEdit-block .date .el-input__inner').forEach(el=>{
                        el.style.height = this.addEditForm.dateHeight
                        el.style.color = this.addEditForm.dateFontColor
                        el.style.fontSize = this.addEditForm.dateFontSize
                        el.style.borderWidth = this.addEditForm.dateBorderWidth
                        el.style.borderStyle = this.addEditForm.dateBorderStyle
                        el.style.borderColor = this.addEditForm.dateBorderColor
                        el.style.borderRadius = this.addEditForm.dateBorderRadius
                        el.style.backgroundColor = this.addEditForm.dateBgColor
                    })
                    document.querySelectorAll('.addEdit-block .date .el-form-item__label').forEach(el=>{
                        el.style.lineHeight = this.addEditForm.dateHeight
                        el.style.color = this.addEditForm.dateLableColor
                        el.style.fontSize = this.addEditForm.dateLableFontSize
                    })
                    document.querySelectorAll('.addEdit-block .date .el-input__icon').forEach(el=>{
                        el.style.color = this.addEditForm.dateIconFontColor
                        el.style.fontSize = this.addEditForm.dateIconFontSize
                        el.style.lineHeight = this.addEditForm.dateHeight
                    })
                    // upload
                    let iconLineHeight = parseInt(this.addEditForm.uploadHeight) - parseInt(this.addEditForm.uploadBorderWidth) * 2 + 'px'
                    document.querySelectorAll('.addEdit-block .upload .el-upload--picture-card').forEach(el=>{
                        el.style.width = this.addEditForm.uploadHeight
                        el.style.height = this.addEditForm.uploadHeight
                        el.style.borderWidth = this.addEditForm.uploadBorderWidth
                        el.style.borderStyle = this.addEditForm.uploadBorderStyle
                        el.style.borderColor = this.addEditForm.uploadBorderColor
                        el.style.borderRadius = this.addEditForm.uploadBorderRadius
                        el.style.backgroundColor = this.addEditForm.uploadBgColor
                    })
                    document.querySelectorAll('.addEdit-block .upload .el-form-item__label').forEach(el=>{
                        el.style.lineHeight = this.addEditForm.uploadHeight
                        el.style.color = this.addEditForm.uploadLableColor
                        el.style.fontSize = this.addEditForm.uploadLableFontSize
                    })
                    document.querySelectorAll('.addEdit-block .upload .el-icon-plus').forEach(el=>{
                        el.style.color = this.addEditForm.uploadIconFontColor
                        el.style.fontSize = this.addEditForm.uploadIconFontSize
                        el.style.lineHeight = iconLineHeight
                        el.style.display = 'block'
                    })
                    // 
                    document.querySelectorAll('.addEdit-block .textarea .el-textarea__inner').forEach(el=>{
                        el.style.height = this.addEditForm.textareaHeight
                        el.style.color = this.addEditForm.textareaFontColor
                        el.style.fontSize = this.addEditForm.textareaFontSize
                        el.style.borderWidth = this.addEditForm.textareaBorderWidth
                        el.style.borderStyle = this.addEditForm.textareaBorderStyle
                        el.style.borderColor = this.addEditForm.textareaBorderColor
                        el.style.borderRadius = this.addEditForm.textareaBorderRadius
                        el.style.backgroundColor = this.addEditForm.textareaBgColor
                    })
                    document.querySelectorAll('.addEdit-block .textarea .el-form-item__label').forEach(el=>{
                        // el.style.lineHeight = this.addEditForm.textareaHeight
                        el.style.color = this.addEditForm.textareaLableColor
                        el.style.fontSize = this.addEditForm.textareaLableFontSize
                    })
                    // 
                    document.querySelectorAll('.addEdit-block .btn .btn-success').forEach(el=>{
                        el.style.width = this.addEditForm.btnSaveWidth
                        el.style.height = this.addEditForm.btnSaveHeight
                        el.style.color = this.addEditForm.btnSaveFontColor
                        el.style.fontSize = this.addEditForm.btnSaveFontSize
                        el.style.borderWidth = this.addEditForm.btnSaveBorderWidth
                        el.style.borderStyle = this.addEditForm.btnSaveBorderStyle
                        el.style.borderColor = this.addEditForm.btnSaveBorderColor
                        el.style.borderRadius = this.addEditForm.btnSaveBorderRadius
                        el.style.backgroundColor = this.addEditForm.btnSaveBgColor
                    })
                    // 
                    document.querySelectorAll('.addEdit-block .btn .btn-close').forEach(el=>{
                        el.style.width = this.addEditForm.btnCancelWidth
                        el.style.height = this.addEditForm.btnCancelHeight
                        el.style.color = this.addEditForm.btnCancelFontColor
                        el.style.fontSize = this.addEditForm.btnCancelFontSize
                        el.style.borderWidth = this.addEditForm.btnCancelBorderWidth
                        el.style.borderStyle = this.addEditForm.btnCancelBorderStyle
                        el.style.borderColor = this.addEditForm.btnCancelBorderColor
                        el.style.borderRadius = this.addEditForm.btnCancelBorderRadius
                        el.style.backgroundColor = this.addEditForm.btnCancelBgColor
                    })
                })
            },
            addEditUploadStyleChange() {
                this.$nextTick(()=>{
                    document.querySelectorAll('.addEdit-block .upload .el-upload-list--picture-card .el-upload-list__item').forEach(el=>{
                        el.style.width = this.addEditForm.uploadHeight
                        el.style.height = this.addEditForm.uploadHeight
                        el.style.borderWidth = this.addEditForm.uploadBorderWidth
                        el.style.borderStyle = this.addEditForm.uploadBorderStyle
                        el.style.borderColor = this.addEditForm.uploadBorderColor
                        el.style.borderRadius = this.addEditForm.uploadBorderRadius
                        el.style.backgroundColor = this.addEditForm.uploadBgColor
                    })
                })
            },
        }
    };
</script>
